import React from 'react';

import Color from './Color';

class Game extends React.Component {
  constructor(props) {
    super(props)
  
    this.state = {
      color: {
        red: 150,
        green: 0,
        blue: 100
      }
    }
  }

  randomColor = () => {
    function randomNum() {
      return Math.floor(Math.random() * 256);
    };

    this.setState({
      color: {
        red: randomNum(),
        green: randomNum(),
        blue: randomNum()
      }
    });
  }
  
  render() {
    return (
      <div>
        <Color 
          color={this.state.color}
          randomColor={this.randomColor}
        />
      </div>
    )
  }
}

export default Game;
